.header {
  padding-top: 15px;
  border-bottom: 1px solid #e5e5e5;
}

.header__top {
  display: flex;
  margin-bottom: 15px;
}



.header__left-links {
  width: 50%;
  display: flex;
  align-items: center;
}
.header__logo-link {
  display: block;
  width: 247px;
  height: 58px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url('../media/images/svg/napopravku-logo.svg');
}
.header__city-select {
  display: inline-block;
  margin-left: 15px;
  font-size: 14px;
  color: #757575;
  position: relative;
  padding-left: 20px;
  i {
    display: inline-block;
    width: 12px;
    height: 14px;
    background-image: url('../media/images/svg/marker-mini.svg');
    background-size: cover;
    position: absolute;
    left: 0;
    top: 2px;
  }
  &::after {
    content: '';
    position: absolute;
    border-color: #4c4c4c transparent transparent;
    border-style: solid;
    border-width: 4px 4px 0;
    height: 0;
    right: -15px;
    margin-top: -2px;
    position: absolute;
    top: 50%;
    width: 0;
  }
  span {
    border-bottom: 1px dashed currentColor;
    display: inline-block;
    line-height: 1.2;
  }
}



.header__right-links {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 28px;
}
.header__info-links, .header__auth-links {
  padding-left: 24px;
  background-repeat: no-repeat;
  background-position: 4px 4px;
  text-align: right;
  margin-bottom: 4px;
  
  a {
    padding-right: 8px;
    font-size: 14px;
    color: #757575;
  }
  span {
    display: inline-block;
    line-height: 1;
  }
}

.header__info-links {
  span {
    border-bottom: 1px solid currentColor;
  }
  a:hover {
    span {
      border-bottom: none;
    }
  }
} 
.header__auth-links {
  margin-left: 30px;
  span {
    border-bottom: 1px dashed currentColor;
  }
  a:hover {
    span {
      border-bottom: none;
    }
  }
} 
.header__feedback {
  width: 100%;
  text-align: right;
  a {
    color: #ffa601;
    font-size: 14px;
    display: inline-block;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-right: 30px;
    background-repeat: no-repeat;
    background-position: right 2px;
    span {
      display: inline-block;
      line-height: 1;
      border-bottom: 1px solid currentColor;
    }
    &:hover {
      span {
        border-bottom: none;
      }
    }

  }
}


.header__nav {
  position: relative;
  &>.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.header__nav-links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 20px;
  &>li {
    display: inline-block;
    &:hover {
      background-color: #40b3b7;
      &>a {
        color: #fff;
        cursor: pointer;
      }
    }
    &.active>.header__dropdown {
      display: block;
    }
    &>a {
      display: block;
      padding-top: 10px;
      padding-bottom: 24px;
      padding-left: 14px;
      padding-right: 14px;
    }
  }
}


.header__dropdown {
  display: none;
  padding-top: 20px;
  position: absolute;
  left: 0;
  width: 100%;
  background-color: #fff;
  border-bottom: solid 2px #40b3b7;
  border-top: solid 2px #40b3b7;
  z-index: 100;
  &>.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    position: relative;
  }
  .btn-close {
    position: absolute;
    right: 10px;
    top: -10px;
  }
}
.header__dropdown-item {
  width: calc(50% - 40px);
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 20px;
  padding-bottom: 20px;
  border: 1px solid rgb(159, 217, 219);
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  position: relative;
  h3 {
    font-size: 20px;
    color: rgb(64, 179, 183);
    margin-top: 0;
    margin-bottom: 18px;
    width: 100%;
  }
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
      font-size: 18px;
      margin-bottom: 12px;
      a {
        text-decoration: underline;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}
.header__dropdown-more {
  color: #8a8a8a;
  font-size: 18px;
  text-decoration: underline;
  position: absolute;
  bottom: 15px;
  right: 20px;
  &:hover {
    text-decoration: none;
  }
}



.header__search {
  form {
    display: flex;
    margin-bottom: 15px;
  }
  button[type="submit"] {
    width: 47px;
    height: 47px;
    background-color: #c7c7c7;
    border: none;
    cursor: pointer;
    background-image: url('../media/images/svg/search-icon.svg');
    background-repeat: no-repeat;
    background-position: center;
  }
}
.header__serch-input {
  height: 47px;
  color: #bababa;
  border: 1px solid #f1f1f1;
  border-right: none;
  padding: 0 20px;
  flex-grow: 1;
}