body {
  color: #333;
  font-family: sans-serif;
  line-height: 1.5;
  font-family: GothamPro, sans-serif;
  * {
    box-sizing: border-box;
  }
}

h1 {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 24px;
}

h1, h2, h3 {
  line-height: 1.2;
}


a {
  color: #000;
  text-decoration: none;
}

.container {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  width: 100%;
  max-width: 1240px;
  margin: auto;

  @media (min-width: 760px) {
    padding-top: 0;
  }
}