.useful-article {
  padding: 15px;
  padding-top: 30px;
  border: 1px solid #f1f1f1;
  margin-bottom: 16px;
  @media(min-width: 760px) {
    padding: 30px;
    padding-bottom: 15px;
  }
}

.useful-article__title {
  margin-top: 0;
}

.useful-article__items {
  display: flex;
  flex-wrap: wrap;
} 
.useful-article__item {
  min-width: 120px;
  max-width: 50%;
  margin-bottom: 15px;
  padding: 8px;
  &:hover {
    @media (min-width: 760px) {
      span {
        text-decoration: none;
      }
    }
  }
  span {
    width: 100%;
    text-decoration: underline;
    display: block;
    @media (min-width: 760px) {
      width: auto;
      padding-left: 15px;
    }
  }

  @media (min-width: 760px) {
    width: 33.333333%;
    display: flex;
    align-items: center;
  }
}