.loyality-pane {
  background-color: #40b3b7;
  padding-top: 32px;
  padding-bottom: 32px;
  @media (min-width: 760px) {
    padding-top: 64px;
  }
  .container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @media (min-width: 760px) {
      justify-content: space-between;
    }
  }
}

.loyality-pane__link {
  color: #fff;
  padding-left: 68px;
  font-size: 18px;
  font-weight: bold;
  background-image: url('../media/images/svg/gift-light.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 0 0;
  margin-bottom: 32px;
  span {
    border-bottom: 2px dashed #fff;
  }
}

.loyality-pane__feedback {
  width: 100%;
  padding-left: 16px;
  padding-right: 15px;
  text-align: center;
  .btn {
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 0;
  }
  @media (min-width: 760px) {
    width: auto;
    text-align-last: left;
  }
}