.hamburger-btn {
  width: 50px;
  height: 55px;
  background-image: url('../media/images/svg/burger-btn.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 35px;
  border: none;
  background-color: transparent;
  outline: none;
}

.btn {
  padding-top: 18px;
  padding-bottom: 18px;
  color: #ffffff;
  font-weight: bold;
  background-color: #40b3b7;
  border: none;
  font-size: 18px;
  padding-left: 32px;
  padding-right: 32px;
  cursor: pointer;
  width: auto;
  &_bordered {
    border-radius: 3px;
  }
  &_wide {
    width: 100%;
  }
  &_secondary {
    background-color: #ffa601;
  }
}


.btn-close {
  width: 42px;
  height: 42px;
  border: none;
  background: transparent;
  background-image: url('../media/images/svg/close.svg');
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  cursor: pointer;
  outline: none;
}


.scroll-top-btn {
  display: none;
  width: 48px;
  height: 48px;
  border: none;
  background-color: transparent;
  padding: 0;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  background-image: url('../media/images/svg/scroll-top.svg');
  background-repeat: no-repeat;
  background-position: center;
}