.modal-cont {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  padding: 20px;
  padding-top: 32px;
  overflow: scroll;
  display: none;
  box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.5);
  &::-webkit-scrollbar { 
    display: none; 
  }
  @media (min-width: 760px) {
    max-width: 500px;
    overflow: visible;
    height: auto;
    left: calc(50% - 250px);
    top: 30px;
    margin: auto;
    max-height: calc(100vh - 30px);
    overflow-y: scroll;
  }
  margin: auto;
  h2 {
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 16px;
  } 
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
}

.form__policy {
  a {
    text-decoration: underline;
  }
}


.form-item {
  margin-bottom: 20px;
  p {
    margin: 0;
    margin-bottom: 4px;
  }
  &:last-of-type {
    margin-bottom: 10px;
  }
  input[type="text"], 
  input[type="password"],
  textarea {
    border: solid 1px #d0d0d0;
    color: #000;
    padding: 14px 10px;
    width: 100%;
    appearance: none;
  }

  &_pass {
    margin-bottom: 10px;
  }
}

.from__social-auth {
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
}
.from__social-icons {
  a {
    display: inline-block;
    width: 54px;
    height: 54px;
    margin-right: 8px;

    i {
      display: inline-block;
      width: 100%;
      height: 100%;
      
      background-size: cover;
      background-repeat: no-repeat;
      background-position: ceneter;
    }
  }
}


.pass-restore {
  border-bottom: 1px dashed currentColor;
  span {
    font-size: 14px;
  }
}

.checkbox-group {
  font-size: 14px;
  label {
    position: relative;
    display: block;
  }
  i {
    position: absolute;
    left: 0;
    top: 2px;
    width: 15px;
    height: 15px;
    display: inline-block;
    background-color: #a0d9db;
    margin-right: 8px;
    &::before {
      content: '';
      position: absolute;
      width: 18px;
      height: 16px;
      background-image: url('../media/images/svg/checked.svg');
      opacity: 0;
      bottom: 2px;
      left: 1px;
      transform: scale(0.8);
      transition: opacity 150ms ease, transform 150ms ease;
    }
  }
  span {
    display: inline-block;
    padding-left: 20px;
    line-height: 21px;
  }

  input:checked+label {
    i {
      &::before {
        opacity: 1;
        transform: scale(1);
      }
    }
  } 
}


.form__tabs-nav {
  padding: 0;
  margin: 0;
  list-style-type: none;
  margin-bottom: 24px;
  li {
    display: inline-block;
    position: relative;
    font-size: 18px;
    &:not(:last-of-type) {
      padding-right: 20px;
      &::after {
        content: '/';
        position: absolute;
        top: 0;
        right: 2px;
      }
    }
    a {
      border-bottom: 1px dashed #000;
    }
    &.is-active {
      a {
        border-bottom: none;
      }
    }
  }
}


.form__tabs {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.form__tabs-item {
  display: none;
}

.form__radio-group {
  label {
    margin-left: 8px;
  }
  i {
    width: 16px;
    height: 16px;
    background-color: #d7d7d7;
    border-radius: 50%;
    display: inline-block;
    transform: translateY(3px);
    position: relative;
  }
  input:checked+i {
    background-color: #40b3b7;
    &::before {
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.form__policy {
  font-size: 14px;
  a {
    text-decoration: underline;
  }
}


.form__custom-select {
  width: 100%;
  background-color: #b3e1e2;
  border: none;
  height: 48px;
  padding-left: 10px;
  display: block;
  outline: none;
  padding-right: 10px;
  overflow:hidden; 
  white-space:nowrap; 
  text-overflow:ellipsis;
  option {
    text-overflow:ellipsis;
    overflow:hidden;
  }
}

.modal-cont__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
}


.form-item__stars-cont {
  display: flex;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}
.form-item__stars {
  margin-right: 8px;
}
.form-item__stars-label {
  font-size: 20px;
  margin-top: 4px;
  margin-right: 8px;
}
.form-item__stars-status {
  font-size: 20px;
  margin-top: 4px;
}