.hide-overflow {
  overflow: hidden;
}

.content-right {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.content-center {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

body {
  color: #333;
  font-family: sans-serif;
  line-height: 1.5;
  font-family: GothamPro, sans-serif;
}

body * {
  box-sizing: border-box;
}

h1 {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 24px;
}

h1, h2, h3 {
  line-height: 1.2;
}

a {
  color: #000;
  text-decoration: none;
}

.container {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  width: 100%;
  max-width: 1240px;
  margin: auto;
}

@media (min-width: 760px) {
  .container {
    padding-top: 0;
  }
}

@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/gothamPro/GothamPro-Medium.eot");
  src: local("../fonts/gothamPro/Gotham Pro Medium"), local("GothamPro-Medium"), url("../fonts/gothamPro/GothamPro-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/gothamPro/GothamPro-Medium.woff") format("woff"), url("../fonts/gothamPro/GothamPro-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/gothamPro/GothamPro.eot");
  src: local("../fonts/gothamPro/Gotham Pro"), local("GothamPro"), url("../fonts/gothamPro/GothamPro.eot?#iefix") format("embedded-opentype"), url("../fonts/gothamPro/GothamPro.woff") format("woff"), url("../fonts/gothamPro/GothamPro.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.fade-scale-enter-active, .fade-scale-leave-active {
  transition: opacity .3s, transform .3s;
}

.fade-scale-enter, .fade-scale-leave-to {
  opacity: 0;
  transform: scale(0.9);
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.header {
  padding-top: 15px;
  border-bottom: 1px solid #e5e5e5;
}

.header__top {
  display: flex;
  margin-bottom: 15px;
}

.header__left-links {
  width: 50%;
  display: flex;
  align-items: center;
}

.header__logo-link {
  display: block;
  width: 247px;
  height: 58px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../media/images/svg/napopravku-logo.svg");
}

.header__city-select {
  display: inline-block;
  margin-left: 15px;
  font-size: 14px;
  color: #757575;
  position: relative;
  padding-left: 20px;
}

.header__city-select i {
  display: inline-block;
  width: 12px;
  height: 14px;
  background-image: url("../media/images/svg/marker-mini.svg");
  background-size: cover;
  position: absolute;
  left: 0;
  top: 2px;
}

.header__city-select::after {
  content: '';
  position: absolute;
  border-color: #4c4c4c transparent transparent;
  border-style: solid;
  border-width: 4px 4px 0;
  height: 0;
  right: -15px;
  margin-top: -2px;
  position: absolute;
  top: 50%;
  width: 0;
}

.header__city-select span {
  border-bottom: 1px dashed currentColor;
  display: inline-block;
  line-height: 1.2;
}

.header__right-links {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 28px;
}

.header__info-links, .header__auth-links {
  padding-left: 24px;
  background-repeat: no-repeat;
  background-position: 4px 4px;
  text-align: right;
  margin-bottom: 4px;
}

.header__info-links a, .header__auth-links a {
  padding-right: 8px;
  font-size: 14px;
  color: #757575;
}

.header__info-links span, .header__auth-links span {
  display: inline-block;
  line-height: 1;
}

.header__info-links span {
  border-bottom: 1px solid currentColor;
}

.header__info-links a:hover span {
  border-bottom: none;
}

.header__auth-links {
  margin-left: 30px;
}

.header__auth-links span {
  border-bottom: 1px dashed currentColor;
}

.header__auth-links a:hover span {
  border-bottom: none;
}

.header__feedback {
  width: 100%;
  text-align: right;
}

.header__feedback a {
  color: #ffa601;
  font-size: 14px;
  display: inline-block;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 30px;
  background-repeat: no-repeat;
  background-position: right 2px;
}

.header__feedback a span {
  display: inline-block;
  line-height: 1;
  border-bottom: 1px solid currentColor;
}

.header__feedback a:hover span {
  border-bottom: none;
}

.header__nav {
  position: relative;
}

.header__nav > .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__nav-links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 20px;
}

.header__nav-links > li {
  display: inline-block;
}

.header__nav-links > li:hover {
  background-color: #40b3b7;
}

.header__nav-links > li:hover > a {
  color: #fff;
  cursor: pointer;
}

.header__nav-links > li.active > .header__dropdown {
  display: block;
}

.header__nav-links > li > a {
  display: block;
  padding-top: 10px;
  padding-bottom: 24px;
  padding-left: 14px;
  padding-right: 14px;
}

.header__dropdown {
  display: none;
  padding-top: 20px;
  position: absolute;
  left: 0;
  width: 100%;
  background-color: #fff;
  border-bottom: solid 2px #40b3b7;
  border-top: solid 2px #40b3b7;
  z-index: 100;
}

.header__dropdown > .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  position: relative;
}

.header__dropdown .btn-close {
  position: absolute;
  right: 10px;
  top: -10px;
}

.header__dropdown-item {
  width: calc(50% - 40px);
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 20px;
  padding-bottom: 20px;
  border: 1px solid #9fd9db;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  position: relative;
}

.header__dropdown-item h3 {
  font-size: 20px;
  color: #40b3b7;
  margin-top: 0;
  margin-bottom: 18px;
  width: 100%;
}

.header__dropdown-item ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.header__dropdown-item ul li {
  font-size: 18px;
  margin-bottom: 12px;
}

.header__dropdown-item ul li a {
  text-decoration: underline;
}

.header__dropdown-item ul li a:hover {
  text-decoration: none;
}

.header__dropdown-more {
  color: #8a8a8a;
  font-size: 18px;
  text-decoration: underline;
  position: absolute;
  bottom: 15px;
  right: 20px;
}

.header__dropdown-more:hover {
  text-decoration: none;
}

.header__search form {
  display: flex;
  margin-bottom: 15px;
}

.header__search button[type="submit"] {
  width: 47px;
  height: 47px;
  background-color: #c7c7c7;
  border: none;
  cursor: pointer;
  background-image: url("../media/images/svg/search-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.header__serch-input {
  height: 47px;
  color: #bababa;
  border: 1px solid #f1f1f1;
  border-right: none;
  padding: 0 20px;
  flex-grow: 1;
}

.bottom-links {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #efefef;
}

.bottom-links h2 {
  margin-top: 0;
  margin-bottom: 28px;
}

.bottom-links__row {
  display: flex;
  flex-wrap: wrap;
}

.bottom-links__row.collapsed {
  display: none;
}

.bottom-links__row.collapsed + .bottom-links__row > .bottom-links__more::after {
  border-top: 4px solid #777;
  border-bottom: none;
}

.bottom-links__row ul {
  padding: 0;
  margin: 0;
  margin-right: 16px;
  width: calc(25% - 16px);
  list-style-type: none;
}

.bottom-links__row ul li {
  margin-bottom: 8px;
}

.bottom-links__more {
  position: relative;
  color: #777;
  text-decoration: underline;
}

.bottom-links__more:hover {
  text-decoration: none;
}

.bottom-links__more::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  top: 40%;
  margin-left: 5px;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #777;
  border-top: none;
}

.footer {
  padding-top: 60px;
  padding-bottom: 45px;
  background-color: #efefef;
}

.footer__links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.footer__links-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  list-style-type: none;
}

.footer__links-list li {
  display: inline-block;
  margin-right: 16px;
}

.footer__links-list li a:hover {
  text-decoration: underline;
}

.footer__copyright p {
  font-size: 12px;
  color: #949494;
}

.footer__social-links {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.footer__social-links li {
  display: inline-block;
}

.footer__social-links li a {
  display: inline-block;
  width: 35px;
  height: 35px;
  margin-left: 10px;
  background-color: #009aa8;
  transition: box-shadow ease 200ms;
  background-position: center;
  background-repeat: no-repeat;
}

.footer__social-links li a:hover {
  box-shadow: 0 -2px 0 #009aa8, 0 2px 0 #009aa8;
}

.hamburger-btn {
  width: 50px;
  height: 55px;
  background-image: url("../media/images/svg/burger-btn.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 35px;
  border: none;
  background-color: transparent;
  outline: none;
}

.btn {
  padding-top: 18px;
  padding-bottom: 18px;
  color: #ffffff;
  font-weight: bold;
  background-color: #40b3b7;
  border: none;
  font-size: 18px;
  padding-left: 32px;
  padding-right: 32px;
  cursor: pointer;
  width: auto;
}

.btn_bordered {
  border-radius: 3px;
}

.btn_wide {
  width: 100%;
}

.btn_secondary {
  background-color: #ffa601;
}

.btn-close {
  width: 42px;
  height: 42px;
  border: none;
  background: transparent;
  background-image: url("../media/images/svg/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  cursor: pointer;
  outline: none;
}

.scroll-top-btn {
  display: none;
  width: 48px;
  height: 48px;
  border: none;
  background-color: transparent;
  padding: 0;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  background-image: url("../media/images/svg/scroll-top.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.breadcrumbs ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 1.2;
  margin-top: 16px;
  margin-bottom: 32px;
}

@media (min-width: 760px) {
  .breadcrumbs ul {
    margin-top: 35px;
    margin-bottom: 35px;
  }
}

.breadcrumbs ul li {
  color: #5d5c5c;
  position: relative;
  margin-top: 2px;
  margin-bottom: 4px;
}

.breadcrumbs ul li:not(:first-of-type) {
  padding-left: 22px;
}

.breadcrumbs ul li:not(:first-of-type)::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  left: 10px;
  top: 6px;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-left: 4px solid #7f7f7f;
}

.breadcrumbs ul li a {
  position: relative;
}

.breadcrumbs ul li a::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #b0b0b0;
  bottom: 0;
  left: 0;
}

.date-table {
  display: inline-block;
  background: #c5e8e9;
  font-size: 14px;
  padding: 2px 5px;
  line-height: 1em;
  letter-spacing: .5px;
  margin-top: 15px;
}

.anchor-links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 14px;
}

.anchor-links li {
  margin-bottom: 8px;
}

.anchor-links a {
  border-bottom: 1px dashed currentColor;
  color: #1bb2c0;
}

.dsecription-list {
  background-color: #f2f2f2;
  padding-top: 32px;
  padding-bottom: 32px;
  padding-left: 54px;
  padding-right: 16px;
  background-image: url("../media/images/svg/quote-sign.svg");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: 14px 42px;
}

.dsecription-list dt {
  font-weight: bold;
  margin-bottom: 24px;
}

.dsecription-list dd {
  margin: 0;
}

.common-table {
  width: 100%;
  max-width: 600px;
  vertical-align: baseline;
  font-size: 14px;
  border-collapse: collapse;
}

.common-table th {
  background-color: #f2f2f2;
  border: 1px solid #bdbdbd;
  padding: 4px;
}

.common-table td {
  border: 1px solid #bdbdbd;
  padding: 4px;
}

.loyality-pane {
  background-color: #40b3b7;
  padding-top: 32px;
  padding-bottom: 32px;
}

@media (min-width: 760px) {
  .loyality-pane {
    padding-top: 64px;
  }
}

.loyality-pane .container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

@media (min-width: 760px) {
  .loyality-pane .container {
    justify-content: space-between;
  }
}

.loyality-pane__link {
  color: #fff;
  padding-left: 68px;
  font-size: 18px;
  font-weight: bold;
  background-image: url("../media/images/svg/gift-light.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 0 0;
  margin-bottom: 32px;
}

.loyality-pane__link span {
  border-bottom: 2px dashed #fff;
}

.loyality-pane__feedback {
  width: 100%;
  padding-left: 16px;
  padding-right: 15px;
  text-align: center;
}

.loyality-pane__feedback .btn {
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 0;
}

@media (min-width: 760px) {
  .loyality-pane__feedback {
    width: auto;
    text-align-last: left;
  }
}

