.checkbox-group {
  font-size: 14px;
}

.checkbox-group label {
  position: relative;
  display: block;
}

.checkbox-group i {
  position: absolute;
  left: 0;
  top: 2px;
  width: 15px;
  height: 15px;
  display: inline-block;
  background-color: #a0d9db;
  margin-right: 8px;
}

.checkbox-group i::before {
  content: '';
  position: absolute;
  width: 18px;
  height: 16px;
  background-image: url("../media/images/svg/checked.svg");
  opacity: 0;
  bottom: 2px;
  left: 1px;
  transform: scale(0.8);
  transition: opacity 150ms ease, transform 150ms ease;
}

.checkbox-group span {
  display: inline-block;
  padding-left: 20px;
  line-height: 21px;
}

.checkbox-group input:checked + label i::before {
  opacity: 1;
  transform: scale(1);
}

