.breadcrumbs {
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    font-size: 16px;
    line-height: 1.2;
    margin-top: 16px;
    margin-bottom: 32px;
    @media (min-width: 760px) {
      margin-top: 35px;
      margin-bottom: 35px;
    }
    li {
      color: #5d5c5c;
      position: relative;
      margin-top: 2px;
      margin-bottom: 4px;
      &:not(:first-of-type) {
        padding-left: 22px;
        &::before {
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          left: 10px;
          top: 6px;
          border-top: 4px solid transparent;
          border-bottom: 4px solid transparent;
          border-left: 4px solid #7f7f7f;
        }
      }

      a {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          background-color: #b0b0b0;
          bottom: 0;
          left: 0;
        }
      }
    }
  }
}