.comment-card {
  padding: 20px;
  display: inline-flex;
  flex-wrap: wrap;
  align-content: flex-start;
  align-items: flex-start;

  @media (min-width: 760px) {
    flex-wrap: nowrap;
  }

  @for $i from 0 through 5 {
    &.level-#{$i} {
      @if $i > 0 {
        border: 1px solid #40b3b7;
        margin-left: 20px + ($i * 10);
        @media (min-width: 760px) {
          margin-left: 20px + ($i * 100);
        }
      } 
    }
  }
}

.comment-card__pic {
  flex-basis: content;
}

.comment-card__content {
  margin-left: 0;
  @media (min-width: 760px) {
    margin-left: 15px;
  }
}

.comment-card__name {
  p {
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 8px;
  }
}

.comment-card__date {
  p {
    font-size: 14px;
    color: #aaa;
    margin-bottom: 8px;
    margin-top: 8px;
  }
}

.comment-card__answer-form-cont {
  display: flex;
  flex-wrap: nowrap;
  @media (max-width: 760px) {
    flex-wrap: wrap;
  }
}

.comment-card__form {
  width: 100%;
  margin-left: 15px;
  @media (max-width: 760px) {
    margin-left: 0;
  }

  input[type="text"], input[type="email"], textarea {
    width: 100%;
    border: 1px solid #bbd1d1;
    margin-bottom: 5px;
    font-size: 22px;
    line-height: 22px;
    padding: 12px 21px;
  }

  .btn {
    @media (min-width: 760px) {
      width: auto;
      padding-left: 32px;
      padding-right: 32px;
    }
  }
}

.comment-card__answer-open {
  color: #777;
  text-decoration: none;
  border-bottom: 1px dashed #777;
  font-size: 14px;
  white-space: nowrap;
  &:hover {
    @media (min-width: 760px) {
      border-bottom: none;
    }
  }
}

.comment-card__answer-slide-cont {
  display: none;
}

.comment-card__answer-form-cont {
  padding-top: 15px;
  padding-right: 15px;
}