.bottom-links {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #efefef;
  h2 {
    margin-top: 0;
    margin-bottom: 28px;
  }
}
.bottom-links__row {
  display: flex;
  flex-wrap: wrap;
  &.collapsed {
    display: none;
    &+.bottom-links__row>.bottom-links__more::after {
      border-top: 4px solid #777;
      border-bottom: none;
    }
  }
  ul {
    padding: 0;
    margin: 0;
    margin-right: 16px;
    width: calc(25% - 16px);
    list-style-type: none;
    li {
      margin-bottom: 8px;
    }
  }
}

.bottom-links__more {
  position: relative;
  color: #777;
  text-decoration: underline;
  &:hover {
    text-decoration: none;
  }
  &::after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top: 40%;
    margin-left: 5px;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-bottom: 4px solid #777;
    border-top: none;
  }
}