.footer {
  padding-top: 60px;
  padding-bottom: 45px;
  background-color: #efefef;
}

.footer__links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.footer__links-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  list-style-type: none;
  li {
    display: inline-block;
    margin-right: 16px;
    a {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.footer__copyright {
  p {
    font-size: 12px;
    color: #949494;
  }
}




.footer__social-links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  li {
    display: inline-block;
    a {
      display: inline-block;
      width: 35px;
      height: 35px;
      margin-left: 10px;
      background-color: #009aa8;
      transition: box-shadow ease 200ms;
      background-position: center;
      background-repeat: no-repeat;
      &:hover {
        box-shadow: 0 -2px 0 #009aa8, 0 2px 0 #009aa8;
      }
    }
  }
}