.text-article {
  padding-bottom: 16px;

  h2 {
    border-bottom: 1px solid #bcbcbc;
    padding-bottom: 4px;
  }

  a {
    color: #1bb2c0;
    text-decoration: underline;
  }
}

.text-article__list {
  padding-left: 20px;
  li {
    margin-bottom: 4px;
  }
}

.text-article__image {
  margin-bottom: 8px;
  img {
    width: 100%;
    @media(min-width: 760px) {
      width: auto;
    }
  }

  &_left {
    float: left;
    margin-right: 16px;
  }

  &_right {
    float: right;
    margin-left: 16px;
  }
}